import uniTag from '@/components/uni-tag/uni-tag.vue'
export default {
    components: {
        uniTag
    },
    data () {
        return {
            title: 'list-triplex-row',
            lists: [],
            type: 'default',
            inverted: false,
            bool: true
        }
    },
    onLoad () {
        let list = [];
        for (let i = 0; i < 5; i++) {
            list.push(i)
        }
        this.lists = [{
            name: '广州市第一小学',
        }, {
            name: '广州市第二小学',
        }, {
            name: '广州市第三小学',
        }];
    },
    methods: {
        handleRemoveSchool () {
            uni.showToast({
                icon: 'success',
                position: 'bottom',
                title: '清除成功'
            });
            // this.lists = []
            this.bool = false
        }
    }
}
